<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome'  }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区 -->
    <el-card>
      <!-- 搜索与添加商品 -->
      <div>
        <el-input
          placeholder="请输入内容"
          v-model="goodsInfo.query"
          ref="search"
          style="width:450px;margin-right:15px"
          clearable
          @clear="getGoodsList"
        >
          <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
        </el-input>
        <el-button type="primary" @click="addGoodsInfo">添加商品</el-button>
      </div>
      <!-- 表单 -->
      <el-table :data="goodsTableData" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label="商品名称"></el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)" width="125px"></el-table-column>
        <el-table-column prop="goods_weight" label="商品重量" width="100px"></el-table-column>
        <el-table-column prop="add_time" label="创建时间" width="170px">
          <template slot-scope="scope">{{scope.row.add_time | dateFormat}}</template>
        </el-table-column>
        <!-- 编辑与删除 -->
        <el-table-column label="操作" width="160px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteGoods(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="goodsInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="goodsInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        style="margin-top:15px"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'List',
  data() {
    return {
      goodsTableData: [],
      //请求参数
      goodsInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
      },
      total: 0,
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    //获取商品列表
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.goodsInfo,
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('发生了未知错误！')
      }
      console.log(res.data)
      this.goodsTableData = res.data.goods
      this.total = res.data.total
    },
    //监听尺寸改变
    handleSizeChange(newSize) {
      this.goodsInfo.pagesize = newSize
      this.getGoodsList()
    },
    //监听当前页改变
    handleCurrentChange(newSize) {
      this.goodsInfo.pagenum = newSize
      this.getGoodsList()
    },
    //监听搜索点击
    //监听删除点击
    async deleteGoods(row) {
      const confirm = await this.$confirm(
        '此操作将永久删除该商品, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((error) => error)

      if (confirm !== 'confirm') {
        return this.$message.info('已取消删除！')
      }

      console.log(row)
      const { data: res } = await this.$http.delete(`goods/${row.goods_id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('发生了未知错误！,删除失败！')
      }
      this.getGoodsList()
      return this.$message.success('删除成功！')
    },
    //添加商品信息
    addGoodsInfo() {
      this.$router.push('/goods/add')
    },
  },
}
</script>

