<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome'  }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区 -->
    <el-card>
      <!-- 添加商品信息提示 -->
      <el-alert title="添加商品信息" center type="info" show-icon :closable="false"></el-alert>
      <!-- 步骤条 -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" :align-center="true">
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- 步骤标签页 -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="ruleFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeTabLeave"
          @tab-click="tabClick"
        >
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-cascader
              v-model="addForm.goods_cat"
              :options="cateList"
              :props="cateProps"
              @change="CateChange"
            ></el-cascader>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <!-- 渲染表单item项 -->
            <el-form-item
              :label="item.attr_name"
              :key="item.attr_id"
              v-for="item in this.manyTableData"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox border :label="cb" v-for="(cb,i) in item.attr_vals" :key="i"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <!-- 商品属性 -->
            <el-form-item
              :label="onlyItem.attr_name"
              v-for="(onlyItem,i) in this.onlyTableData"
              :key="i"
            >
              <el-input v-model="onlyItem.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- 商品图片 -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="upSuccess"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 商品内容，富文本编辑器 -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!-- 添加商品按钮 -->
            <el-button type="primary" class="addGoodsBtn" @click="clickAddForm">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 预览图片对话框 -->
    <el-dialog title="提示" :visible.sync="previewDialogVisible" width="40%">
      <el-image :src="previewPath" style="width:100%"></el-image>
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data() {
    return {
      //当前步骤索引
      activeIndex: '0',
      //添加商品的表单数据对象
      addForm: {
        goods_name: '',
        goods_price: '',
        goods_weight: '',
        goods_number: '',
        //级联选择器双向绑定到的数组
        goods_cat: [],
        //上传成功的路径
        pics: [],
        //商品介绍
        goods_introduce: '',
        attrs: [],
      },
      //添加商品的表单数据对象规则
      addFormRules: {
        goods_name: [
          {
            required: true,
            message: '请输入商品名称！',
            tigger: 'blur',
          },
        ],
        goods_price: [
          {
            required: true,
            message: '请输入商品价格！',
            tigger: 'blur',
          },
        ],
        goods_weight: [
          {
            required: true,
            message: '请输入商品重量！',
            tigger: 'blur',
          },
        ],
        goods_number: [
          {
            required: true,
            message: '请输入商品数量！',
            tigger: 'blur',
          },
        ],
      },
      //商品分类列表
      cateList: [],
      //级联选择框的配置对象
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover',
      },
      //多选框组many
      checkManyList: [],
      //动态参数列表数据
      manyTableData: [],
      //静态属性列表数据
      onlyTableData: [],
      //上传文件图片
      uploadURL: 'https://www.liulongbin.top:8888/api/private/v1/upload',
      //上传图片请求token
      headersObj: {
        Authorization: window.sessionStorage.getItem('token'),
      },
      //预览图片路径
      previewPath: '',
      //预览图片对话框
      previewDialogVisible: false,
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    //获取所有商品分类数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        console.log(res)
        return this.$message.error('发生了未知错误！,获取失败！')
      }
      this.cateList = res.data
      console.log(res)
      console.log(this.cateList)
    },
    //级联经过改变
    CateChange() {
      console.log(this.addForm)
      console.log(this.addForm.goods_cat)
      if (this.addForm.goods_cat.length !== 3) {
        return (
          (this.addForm.goods_cat = []),
          this.$message.error('分类必须为三级分类！')
        )
      }
    },
    //tab切换
    beforeTabLeave(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请先选择商品分类！')
        return false
      }
    },
    //添加商品的tab切换
    async tabClick() {
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'many',
            },
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('发生了未知错误！,获取分类列表失败！')
        }
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.checkManyList = res.data
        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'only',
            },
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('发生了未知错误！,获取商品属性！')
        }
        console.log(res.data)
        this.onlyTableData = res.data
      }
    },
    //处理图片预览效果
    handlePreview(file) {
      this.previewPath = file.response.data.url
      this.previewDialogVisible = true
      console.log(file)
    },
    //处理移除图片的操作
    handleRemove(file) {
      //获取将要删除的文件的临时路径
      console.log(file)
      const filePath = file.response.data.tmp_path
      //从pics数组中找到对应的索引值
      const i = this.addForm.pics.findIndex((x) => x.pic === filePath)
      //调用splice方法，把图片信息对象从pics数组中移除
      this.addForm.pics.splice(i, 1)
    },
    //上传成功时的函数
    upSuccess(response, file, fileList) {
      const picInfo = { pic: response.data.tmp_path }
      this.addForm.pics.push(picInfo)
    },
    clickAddForm() {
      this.$refs.ruleFormRef.validate(async (valid) => {
        if (!valid) this.$message.error('请填写必要的表单项！')

        //执行添加的业务逻辑
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        //处理动态参数
        this.manyTableData.forEach((item) => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        //处理静态属性
        this.onlyTableData.forEach((item) => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_value }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        //发起请求添加商品
        const { data: res } = await this.$http.post('goods', form)
        console.log(res)
        console.log(form)
        if (res.meta.status !== 201) {
          return this.$message.error('发生了未知错误!添加商品失败')
        }
      })
      this.$message.success('添加商品成功！')
      this.$router.push('/goods')
    },
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    },
  },
}
</script>

<style scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.addGoodsBtn {
  margin-top: 15px;
}
</style>